@font-face {
	font-family: 'Segoe UI.woff';
	src: url('./fonts/Segoe\ UI.woff');
}



html {
	 height: 100%;
	 margin: 0;
	 padding: 0;
	
}
 body {
	 font: 100% Roboto, sans-serif;
	 margin: 0;
	 color: #333;
	 font-size: 1rem;
	 display: flex;
	 align-items: center;
	 font-family: 'Segoe UI';
	 font-weight: 200;
	 justify-content: center;
	 height: 100%;
	 background-color: #511730;
}


#quote-box{

display: flex;
flex-direction: column;
border: 5px solid #cb9173;
height: 62.5vh;
width: 62.5vw;
align-items: center;
justify-content: center;





}



 #text {
	 display: flex;
	 align-items: center;
	 justify-content: center;
	 flex-direction: column;
	 color: white;
	 font-size: 1.8rem;
	 text-align: center;

     
	 width: 80%;
	 padding-bottom: 14.7vh;
}
 #quotes {
	 width: 100px;
	 height: 200px;
}
 #author {
	 color: white;
	 margin-right: 40%;
	 font-size: 1.2rem;
	 margin-top: 1rem;
	 font-family: 'Segoe UI';
	 font-weight: 450;
	 
}
 #new-quote {
	 margin-top: 1rem;
}
 #tweet-quote {
	 margin-top: 1rem;
}

.quote-img{
    width:190px;
    height: 143px;

}

.upper-img{

    transform: scale(-1,-1);
    
}


#tweet-quote{


    position: fixed;
    width: 50%;
  right:-250px;
  bottom: 195px;

  
 
}

#tweet-img{
    width:100px;
    height: 82px;
 
   
   
}


#footer-img-container{
    border: 2px solid blue;
    width:70%;
    display: flex;
    justify-content: flex-end;
 
}

button{
	width:230px;
	height: 80px;
	border-radius: 30px;
	background-color: #511730;
	border: 3px solid #CB9173;
	position: absolute;
	bottom: 200px;
}

p{
	font-family: 'Segoe UI';
	font-size: 1.3rem;
	color: white;
	font-weight: 500;

}

button:hover { 
	cursor: pointer;
	background-color: #642C44;
	transition: background-color 0.5s ease;
}



@media only screen and (max-width: 767px) {

	#quote-box{

		display: flex;
		flex-direction: column;
		border: 5px solid #cb9173;
		height: 80vh;
		width:80vw;
		align-items: center;
		justify-content: center;
		
		
		}

		#text {
			margin: 0;
			padding: 0;
			margin-bottom: 16rem;
		}

		
button{
	width:230px;
	height: 80px;
	border-radius: 30px;
	background-color: #511730;
	border: 3px solid #CB9173;
	position: absolute;
	top:50%;
	right:0;
	left:0;
	bottom:0;
	margin: auto;

	

}


#tweet-quote{


    position: absolute;
    width: 100px;
	top:55%;
	right:0;
	left:0;
	margin: auto;
	bottom:0;
	
	display: flex;
	justify-content: center;



	height: 82px;
	align-items: center;

  
 
}


#tweet-img{
    width:100px;
    height: 82px;
	top:0;
	right:0;
	left:0;
	bottom:0;

 
   
   
}


}

